import React from "react";
import styled from "@emotion/styled";

const StyledTag = styled("div")`
    background-color: #035b96;
    color: #4ecca3;
    border-radius: 0.3em;
    padding: 0.2rem 0.4rem;
    margin-right: 1rem;
    whitespace: no-wrap;
    font-size: 85%;

    p{
        margin:0;
    }

`;

const Tag = (props) => {
    return (
        <StyledTag>
            <p>{props.tagFor}</p>
        </StyledTag>
    );
}

export default Tag;
